/* Responsive */
@media (max-width: 1300px) {
  .responsiveMenu {
    display: block;
  }
  .spinner-master * {
    transition: all 0.3s;
    box-sizing: border-box;
  }
  .spinner-master {
    position: relative;
    width: 46px;
    display: flex;
    align-items: center;
    background: transparent;
    height: 46px;
    justify-content: center;
    margin-left: 5px;
    z-index: 9999;
  }
  .spinner:nth-child(2n) {
    width: 70% !important;
  }
  .spinner-master label {
    cursor: pointer;
    height: auto;
    width: 100%;
    padding: 0;
    transform: translateY(0%);
    top: -13px;
    position: relative;
  }
  .spinner-master .spinner {
    height: 2px;
    width: 100%;
    background-color: #ffffff;
  }
  .spinner-master .horizontal {
    margin-top: 6px;
  }
  .spinner-master .diagonal.part-2 {
    margin-top: 6px;
  }
  .spinner-master.active .spinner-spin > .horizontal {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
  .spinner-master.active .spinner-spin > .diagonal.part-1 {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    margin-top: 3px;
  }
  .spinner-master.active .spinner-spin > .diagonal.part-2 {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    margin-top: -10px;
  }
  nav#mobileMenu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: block;
    z-index: 99;
  }
  nav#mobileMenu {
    overflow: hidden;
    max-height: 0;
    background-color: #000;
    transition: all 0.4s ease 0s;
  }
  nav#mobileMenu.active,
  nav#mobileMenu > ul ul.show {
    max-height: 55em;
    overflow: auto;
    padding: 10px 20px;
  }
  nav#mobileMenu > ul ul.show {
    padding: 0;
  }
  nav#mobileMenu > ul > li {
    display: block !important;
    margin: 0 !important;
  }
  nav#mobileMenu > ul > li > a,
  nav#mobileMenu > ul > li > p {
    display: block;
    position: relative;
    padding: 10px 0;
    color: #fff;
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
  }
  nav#mobileMenu > ul > li:last-child > a {
    border-bottom: 0px;
  }
  nav#mobileMenu li.has-submenu > a:before,
  .mainmenu ul > li.active a:before {
    display: none;
  }
  .megamenu.active {
    padding: 15px 0 !important;
  }
  .sub-menu:before {
    height: 90% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
  }
  .sub-menu:after {
    display: none;
  }
  nav#mobileMenu li.has-submenu > a.active:after {
    content: "-";
  }
  nav#mobileMenu ul ul > li a {
    padding: 7px 20px;
    font-size: 16px;
    color: #fff;
  }
  nav#mobileMenu ul ul > li a,
  nav#mobileMenu ul ul > li p {
    padding: 10px 20px;
    line-height: normal;
    -webkit-text-fill-color: #fff;
    display: block;
  }
  .mainmenu ul li ul li {
    border: none;
  }

  nav#mobileMenu > ul > li > a,
  nav#mobileMenu > ul > li > p {
    padding: 10px;
    background: transparent;
    -webkit-text-fill-color: #fff;
    font-size: 14px;
    letter-spacing: 1px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.13);
    text-transform: uppercase;
    font-weight: 500;
  }
  nav#mobileMenu p {
    margin: 0;
    line-height: normal;
  }
}

/* ===================== HEADER ======================*/

.header {
  position: absolute;
  z-index: 99;
  width: 100%;
  padding-right: 5%;
}

.header:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: #fff;
  content: "";
  z-index: -1;
}

.header:before {
  border-color: #222777 transparent;
  border-style: solid;
  border-width: 0 60px 100px;
  content: "";
  position: absolute;
  width: 1150px;
  z-index: 1;
}

.headerRight:before {
  left: -700px;
}
.headerLeft:before {
  right: -700px;
}

.header .container {
  margin-left: 0;
}

.header .col-lg-12 {
  padding-left: 0;
}

.header-inn {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.site-logo a {
  padding: 25px 15px;
  background: #fff;
  width: 195px;
  display: inline-block;
}

.header-navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1;
  position: relative;
  z-index: 11;
}

.mainmenu ul {
  padding-left: 50px;
}

.mainmenu ul li {
  display: inline-block;
  list-style: none;
  margin-right: 20px;
  position: relative;
}

.mainmenu ul li a {
  font-weight: 500;
  color: #000;
  text-transform: uppercase;
  display: block;
  padding: 38px 0;
  position: relative;
  background: linear-gradient(to right, #222777, #222777 50%, #000 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
}
.mainmenu ul li a.lang {
  color: #fff;
  display: block;
  -webkit-text-fill-color: #fff;
  font-weight: 500;
  color: #000;
  text-transform: uppercase;
  display: block;
  padding: 38px 0;
  position: relative;
  background: linear-gradient(to right, #000, #000 50%, #fff 50%);
  -webkit-background-clip: text;
          background-clip: text;
  background-size: 200% 100%;
  background-position: 100%;
}
.mainmenu ul li a.lang i {
  color: #fff;
}

.mainmenu ul > li.active a:before {
  position: absolute;
  content: "";
  background: #222777;
  left: 50%;
  bottom: 25px;
  width: 25px;
  height: 3px;
  border-radius: 5px;
  transform: translateX(-50%);
}

.mainmenu ul li a:hover {
  background-position: 0 100%;
  border-color: #222777;
  /* /* text-decoration: underline; */
  /* margin-bottom: 20px; */
  padding-bottom: 10px;
  /* border-radius: 5px; */
  border-bottom: solid 5px #222777;
}

.mainmenu ul > li.active > a {
  background-position: 0 100%;
  border-color: #222777;
}

.mainmenu ul li ul {
  position: absolute;
  top: 120%;
  /* left: 0; */
  width: auto;
  background: #fff;
  padding: 0;
  transition: all 0.4s ease 0s;
  visibility: hidden;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.mainmenu ul li:hover ul {
  visibility: visible;
  top: 100%;
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.mainmenu ul li ul li {
  margin: 0;
  display: block;
  border-top: 1px solid #eee;
  white-space: nowrap;
}

.mainmenu ul li ul li:hover {
  background: #eee;
}
.mainmenu ul li ul li a {
  display: block;
  padding: 14px 20px;
  text-transform: capitalize;
  font-size: 15px;
}
.mainmenu ul li ul li a:hover {
  border: none;
  color: #222777;
  padding: 14px 20px;
}
.mainmenu ul li ul li span {
  display: block;
  padding: 14px 20px;
  text-transform: capitalize;
  font-size: 15px;
}

.header-btn {
  display: inline-block;
  padding: 16px 25px 16px 40px;
  background: #fff none repeat scroll 0 0;
  border-radius: 5px;
  text-transform: capitalize;
  font-weight: 500;
  margin-left: 35px;
  position: relative;
  color: #222777;
}

.header-btn > div {
  position: absolute;
  left: -16px;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  background: #222777;
  text-align: center;
  color: #222777;
  border-radius: 5px;
  line-height: 40px;
  z-index: 1;
}

.header-btn > div i {
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 8px;
  border-radius: 5px;
}

.header-btn > div:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 4px solid #222777;
  z-index: -1;
  border-radius: 5px;
}

.search-toggle {
  color: #fff;
  padding: 10px;
}
.languageIcon {
  color: #fff;
  padding: 10px;
}
.languageIcon:focus {
  outline: none;
  box-shadow: none;
}

.languageIcon:hover {
  color: #8d8d8e;
}

.header-action {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 35px;
}

#search-overlay {
  transition: opacity 600ms, visibility 600ms;
  opacity: 0;
  visibility: hidden;
}

.block {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  text-align: center;
  background: rgba(0, 0, 0, 0.9);
  margin: 0;
  z-index: 99;
}

.block:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.centered {
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  padding: 10px 15px;
  color: #fff;
  border: none;
  background: transparent;
}

#search-box {
  position: relative;
  width: 100%;
  margin: 0;
}

#search-form {
  height: 4em;
  border-bottom: 1px solid #fff;
  background-color: transparent;
  overflow: hidden;
}

#search-box input[type="text"] {
  width: 100%;
  padding: 20px;
  color: #fff;
  outline: none;
  font-size: 20px;
  background: transparent;
}

#search-button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: auto;
  font-size: 18px;
  color: #fff;
  font-weight: 600;
  text-align: center;
  line-height: 42px;
