.b_info_flex {
  display: flex;
  align-items: center;
}

.blog-left-content h2 {
  margin-bottom: 10px;
  font-size: 50px;
}

.blog-info-img {
  width: 50px;
  background: #111;
  border: 1px solid #111;
}

.blog-info p {
  font-size: 16px;
}

.blog-info p i {
  margin-right: 10px;
  color: #222777;
}

.blog-info-date {
  margin: 0 20px;
}

.blog-left-content-share {
  text-align: center;
  border-bottom: 1px solid #ddd;
  padding-bottom: 30px;
}

.blog-left-content-share ul li {
  display: inline-block;
  margin: 0 3px;
}

.blog-left-content-share ul li button {
  display: block !important;
  background: #111 !important;
  color: #fff !important;
  width: 40px !important;
  height: 40px !important;
  line-height: 40px !important;
}

.blog-left-related-post {
  margin-top: 50px;
}

.blog-left-related-post .news-img {
  margin-top: 20px;
}

.blog-left-related-post .news-text {
  width: 80%;
  padding: 10px;
}

.blog-left-related-post .news-text h3 {
  font-size: 22px;
}

.blog-left-related-post .news-date {
  font-size: 15px;
}

.blog-comment-box {
  margin-left: 100px;
  position: relative;
  margin-bottom: 20px;
}

.blog-comment-people {
  width: 80px;
  position: absolute;
  left: -100px;
}

.blog-comment-people img {
  border-radius: 50%;
}

.blog-comment-text {
  background: #eef3fa;
  padding: 30px;
  border-radius: 30px;
  position: relative;
}

.blog-replay-box {
  margin-left: 200px;
}

.blog-comment-text:before {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  display: block;
  border-style: solid;
  top: 25px;
  border-width: 11px 13px 11px 0;
  border-color: transparent #eef3fa;
  left: -12px;
}

.blog-comment-heading {
  display: flex;
  align-items: center;
}

.blog-comment-date p {
  margin-bottom: 15px;
  color: #111;
}

.blog-comment-date p i {
  margin-right: 10px;
  color: #222777;
}

.blog-leave-comment {
  margin-top: 50px;
}

.comment-form-group {
  position: relative;
  width: 100%;
}

.comment-form-group input {
  background: #eef3fa;
  padding: 0px 10px 0px 40px;
}

.comment-form-text textarea {
  background: #eef3fa;
  height: 200px;
  margin-top: 20px;
  padding: 20px;
}

.comment-form-group i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
}

.blog-comment-submit {
  text-align: center;
  margin-top: 50px;
}

/*Responsive*/
@media (max-width: 767px) {
  .latest-news-area .row .col-lg-6:last-child {
    transform: translateY(0px);
    margin-top: 60px;
  }
  .news-date {
    letter-spacing: 2px;
    font-size: 14px;
  }
  .news-text h3 {
    letter-spacing: 2px;
    font-size: 22px;
  }
  .latest-news-area .row .col-md-6:last-child {
    transform: translateY(0px);
    margin-top: 50px;
  }
  .comment-form-group {
    margin-top: 10px;
  }
}
@media (max-width: 575px) {
  .blog-info {
    display: block;
  }
  .blog-info-img {
    margin-bottom: 10px;
  }
  .b_info_flex {
    display: block;
  }
  .blog-info-date {
    margin: 0;
  }
  .blog-replay-box {
    margin-left: 100px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .news-text h3 {
    letter-spacing: 2px;
    font-size: 22px;
  }
  .news-date {
    font-size: 16px;
    letter-spacing: 1px;
    margin-bottom: 5px;
  }
  .news-text {
    padding: 15px;
  }
  .latest-news-area .row .col-md-6:last-child {
    transform: translateY(-55px);
  }
  .blog-replay-box {
    margin-left: 130px;
  }
  .comment-form-group {
    margin-top: 10px;
  }
}
@media (width: 1024px) {
  .project-box p {
    font-size: 20px;
    left: -110px;
    /* bottom: 150px;
    letter-spacing: 6px; */
  }
  .project-box h3 {
    top: 15px;
    left: 20px;
    letter-spacing: 2px;
    font-size: 30px;
  }

  .project-box:hover .project-zoom {
    transform: scale(1) translateX(-50%, -50%);
  }
}

/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/

body {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #777;
}

.body_wrap {
  overflow: hidden;
  position: relative;
}

.img {
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
}

a,
.button {
  transition: all 0.3s ease-out 0s;
}

a:focus,
.button:focus {
  text-decoration: none;
  outline: none;
}

a {
  text-decoration: none;
}

a:focus,
a:hover,
.gallery-cat a:hover,
.footer-menu li a:hover {
  text-decoration: none;
}

a,
button {
  color: #000;
  outline: medium none;
  cursor: pointer;
}

a:hover {
  color: #222777;
}

button {
  padding: 0px;
  border: none;
  outline: none;
  background: none;
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Teko", sans-serif;
  font-weight: normal;
  color: #3b3c40;
  margin: 0;
  font-style: normal;
  font-weight: 600;
  letter-spacing: 1px;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  font-size: 18px;
  font-weight: normal;
  line-height: 30px;
  color: #777;
  margin-bottom: 15px;
}

p:last-child {
  margin: 0;
}

label {
  color: #7e7e7e;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
}

input,
textarea,
select {
  width: 100%;
  height: 55px;
  background-color: #f8f8f8;
  border: none;
  padding: 0px 20px;
  outline: none;
}

::selection {
  background: #c6a280;
  color: #fff;
  text-shadow: none;
}

*:-ms-input-placeholder {
  color: #999;
  font-size: 16px;
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

*::placeholder {
  color: #999;
  font-size: 16px;
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}
/*--------------------------------------------------------------
#0.2    Global Elements
--------------------------------------------------------------*/

section {
  padding: 100px 0;
}

.cta-btn {
  display: inline-block;
  padding: 15px 35px;
  font-weight: 500;
  text-transform: capitalize;
  border: 1px solid transparent;
}

.btn-fill {
  background: #222777 none repeat scroll 0 0;
  color: #fff;
  padding: 17px 35px;
  border-color: #222777;
}

.btn-fill:hover {
  background: transparent;
  color: #222777;
}

.btn-border {
  border-color: #000;
  color: #000;
}

.site-heading {
  margin-bottom: 30px;
}

.sub-title {
  color: #222777;
  letter-spacing: 5px;
  font-weight: 500;
  margin-bottom: 10px;
  font-size: 24px;
}
html:lang(ar) .sub-title {
  letter-spacing: 0px;
}

.section-title {
  color: #3b3c40;
  font-size: 60px;
  letter-spacing: 2px;
  font-weight: 500;
  line-height: 70px;
}

.form-button {
  display: inline-block;
  background: #222777;
  border: 1px solid #222777;
  padding: 17px 35px;
  color: #fff;
  transition: all 0.4s ease;
}

.form-button:hover {
  background: transparent;
  color: #222777;
}
/*Responsive*/
@media (max-width: 767px) {
  .section-title {
    font-size: 38px;
    line-height: 44px;
  }
  .sub-title {
    letter-spacing: 3px;
    font-size: 20px;
  }
  p {
    font-size: 16px;
    line-height: 26px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .section-title {
    font-size: 45px;
    line-height: 54px;
  }
  p {
    font-size: 16px;
    line-height: 26px;
  }
  .counter-box p {
    letter-spacing: 1px;
  }
}
@media (width: 1024px) {
  .section-title {
    font-size: 50px;
    letter-spacing: 2px;
    line-height: 55px;
  }
}

